import {
  RECEIVE_EVENT_ERRORS,
  CLEAR_ERRORS
} from '../actions/event_actions';

const eventErrorsReducer = (state = [], action) => {
  Object.freeze(state);
  switch (action.type) {
    case RECEIVE_EVENT_ERRORS:
      return action.errors;
    case CLEAR_ERRORS:
      return [];
    default:
      return state;
  }
};

export default eventErrorsReducer;
